import { request } from 'utils';

const prefix = '/api/categories';


export const getStudyFieldGroups = ({
  label,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/StudyFieldGroup',
    method: 'GET',
    params: {
      label,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });

export const createStudyFieldGroups = ({ id, label, isActive = true }) =>
  request({
    prefix,
    url: `/StudyFieldGroup`,
    data: {
      id,
      label,
      isActive,
    },
    method: 'POST',
  });

export const updateStudyFieldGroups = ({ id, label, isActive }) =>
  request({
    prefix,
    url: `/StudyFieldGroup`,
    data: {
      id,
      label,
      isActive,
    },
    method: 'PUT',
  });

export const deleteStudyFieldGroups = ({ id }) =>
  request({
    prefix,
    url: `/StudyFieldGroup`,
    method: 'DELETE',
    data: { id },
  });

export const getStudyFieldGroupsById = ({ id }) =>
  request({
    prefix,
    url: `/StudyFieldGroup/GetById`,
    method: 'GET',
    params: { id },
  });



