import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, Checkbox } from 'antd';
import { requiredRule } from 'utils/validateRules';
import { getQueryParams } from 'utils/urlUtil';
import {
  getStudyFieldGroupsById,
  createStudyFieldGroups,
  updateStudyFieldGroups,
} from 'services/studyFieldGroup';

@Form.create({ name: 'formtudeyFieldGroup' })
class FormStudyFieldGroup extends Component {
  state = {
    studyFieldGroupId: false,
    initialValue: {
      id: '',
      label: '',
      isActive: false,
      isEnable: false,
    },
  };

  componentDidMount = () => {
    this.handleGetParams();
  };

  onChange = e => {
    this.setState({ checked: e.target.checked });
  };

  getBuildingBasesById = async id => {
    this.setState({ studyFieldGroupId: id });
    this.setState({ isEnable: true });
    const res = await getStudyFieldGroupsById({ id });
    this.setState({ checked: res.data.isActive });
    const initialValue = { ...this.state.initialValue, ...res.data };
    this.setState({ initialValue });
  };

  handleGetParams = () => {
    const studyFieldGroupId = getQueryParams().id;
    if (studyFieldGroupId) {
      this.getBuildingBasesById(studyFieldGroupId);
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { studyFieldGroupId } = this.state;
    try {
      if (!studyFieldGroupId) {
        await createStudyFieldGroups({
          id: values.id,
          label: values.label,
          isActive: values.isActive,
        });
      } else {
        await updateStudyFieldGroups({
          id: values.id,
          label: values.label,
          isActive: values.isActive,
        });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue, isEnable, checked } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    return (
      <Card>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit}>
            <Form.Item hasFeedback label="Mã khối ngành" {...formItemLayout}>
              {getFieldDecorator('id', {
                initialValue: initialValue.id,
                rules: [requiredRule],
              })(<Input disabled={isEnable} />)}
            </Form.Item>
            <Form.Item hasFeedback label="Tên khối ngành" {...formItemLayout}>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [requiredRule],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Hiệu lực">
              {getFieldDecorator('isActive', {
                initialValue: `${initialValue.isActive}`,
                // rules: [requiredRule],
              })(<Checkbox onChange={this.onChange} checked={checked} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormStudyFieldGroup {...props} />
  </Page>
));



